<!--BTableSim.vue-->
<template>
  <div class="animated fadeIn">
    <b-card header-tag="header">
      <div slot="header">

        <b-row>
          <b-col>
            <i class="icon-drop"></i> This is BTable_sim
          </b-col>
          <b-col class="text-right">
            <b-dropdown text="View Source" class="m-0 p-0">
              <b-dropdown-item class="p-0" v-for="(x,index) in [
                `/src/components/ComTest2/${name}`,
              ]" :key="index">
                  <com-vs2 :file-path-link=" x " ></com-vs2>
              </b-dropdown-item>
            </b-dropdown>
          </b-col>
        </b-row>



      </div>
      <b-card-body>
        <b-row>

            <button @click="on_add()" class="mb-2">add</button>
            <b-table striped hover small
            :items="[
                {id:1,ten:'thuc 101',que_quan:'QN',ngay_sinh:new Date(), phai_nam:true,tp:'QN' },
                {id:2,ten:'thuc 101',que_quan:'SG',ngay_sinh:new Date(), phai_nam:false,tp:'SG' },
                {id:3,ten:'thuc 101',que_quan:'QN',ngay_sinh:new Date(), phai_nam:true,tp:'QN' },
            ]" 
            :fields=fields            
            >

            <template slot="phai_namc" slot-scope="data">
              <b-form-checkbox
                id="checkbox-1"
                v-model="data.item.phai_nam"
                name="checkbox-1"
                value="true"
                unchecked-value="false"
                disabled
              >
              </b-form-checkbox>
            </template>            

            <template slot="que_quanc" slot-scope="data">
              <b-form-select size="sm" v-model="data.item.que_quan" 
                :options="[
                  {value:'QN',text:'Quảng Ngãi'},
                  {value:'SG',text:'Sài Gòn'}
                ]"
                disabled
              >
              </b-form-select>
            </template>            

            <template slot="cmd" slot-scope="row">
              <button @click="on_edit(row)">
                <i class="fa fa-pencil" aria-hidden="true"></i>
              </button>
              <button @click="on_del(row)">
                <i class="fa fa-trash" aria-hidden="true"></i>
              </button>
            </template>


            </b-table>
        </b-row>

        <b-modal ref="myModalRef" hide-footer :title="(sel.id==0) ? 'Form Add' :'Form Edit' ">
            <div class="d-block">
            <div class="frm text-right">
                <h4>This is form</h4>
                <div>{{sel}}</div>
                <button v-if="sel.id!=0" @click="on_update();">update</button>
                <button v-if="sel.id==0" @click="on_do_add();">add</button>
                <button @click="on_cancel();">cancel</button>
            </div>
            </div>
        </b-modal>


      </b-card-body>
    </b-card>

  </div>
</template>

<script>
//import ColorTheme from './ColorTheme'
import moment from 'moment';

export default {
  name: 'BTableSim.vue',
  components: { 
    //ColorTheme 
  },
  data(){
    return{
      name:`${this.$options.name}`,
      sel:{},
      fields:[
        'id','ten',
        {
          key:'ngay_sinh',
          label:'Ngày Sinh',
            formatter: (value, key, item) => {
              return moment(value).format('DD/MM/YYYY');
            }
        },
        {
          key:'phai_namc',
          label:"Phái Nam"
        },
        {
          key:'que_quanc',
          label:'Quê Quán'
        },
        'cmd'
      ]
    }
  },
  created(){
      //alert( moment().format('DD/MM/YYYY') )
  },
  methods:{
    ngaySinh(data){
      return moment(data).format('DD/MM/YYYY');
    },
    on_add(){
        this.sel={
            id:0
        }
        this.$refs.myModalRef.show();
    },
    on_del(row) {
      let index = this.items.findIndex(x => x.id == row.item.id);
      this.items.splice(index, 1);
    },
    on_edit(row) {
      this.sel = JSON.parse(JSON.stringify(row.item));
      this.$refs.myModalRef.show();
    },
    on_update(){
        let r=Object.assign({},this.sel);
        alert(JSON.stringify(r));
        this.on_cancel();
    },
    on_cancel(){
        this.sel={}
        this.$refs.myModalRef.hide();
    }

  }
}
</script>
